@import '../common/variables.scss';
.footer {
  background-color: #fff;
  display: flex;
  justify-content: center;
  
  
}

.footer__wrapper {
  width: 87%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5% 0;
}

.footer__for-tablet {
  display: none;
}

.footer__questions {
  font-size: 24px;
  color: rgb(40, 54, 69);
  line-height: 1.2;
}

.footer__title {
  font-size: 18px;
  color: rgb(40, 54, 69);
  font-weight: bold;
  line-height: 1.2;
}

.footer__contact {
  font-size: 24px;
  color: rgb(210, 40, 86);
  line-height: 1.2;
  text-align: center;
}

.footer__contacts {
  display: flex;
  flex-direction: column;
}

.footer__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0;
}

.footer__map {
  position: relative;

  &::after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 20%;
    top: -1%;
    background-color: #fff;
    clip-path: polygon(0 0, 50% 100%, 100% 0);
    z-index: 3;
  }
}

#map {
  width: 100%;
  height: 200px;
  background-color: #fff;

  iframe {
    width: 100%;
    height: 100%;
  }
}


@media (min-width: $tablet-width) {
  .footer::after {
    bottom: 30%;
  }

  .footer__wrapper {
    width: 94%;
  }

  .footer__contacts {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .footer__item {
    margin: 0 25px 25px;
    width: 40%;
  }

  .footer__item--address {
    width: 50%;
    margin: 0 auto;
    padding: 3%;
  }

  .footer__questions {
    font-size: 40px;
  }

  .footer__for-tablet {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 35px 0px 20px;
  }

  .footer__link {
    border-style: solid;
    border-width: 3px;
    border-color: #e9ebec;
    border-radius: 50%; 
    height: 50px;
    width: 50px;
    background-repeat: no-repeat;
    background-position: 50%;
  }

  .footer__social {
    display: flex;
    width: 33%;
    justify-content: space-around;
  }

  .footer__link--twitter {
    background-image: url("./../images/twitter.png");
  }

  .footer__link--facebook {
    background-image:url("./../images/facebook.png");
  }

  .footer__link--youtube {
    background-image:url("./../images/youtube.png");
  }

  .footer__academy {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 20%;

    span {
      margin-right: 5px;
    }
  }

  #map {
    height: 375px;
  }

  .footer__map::after {
    top: 0;
  }
}

@media (min-width: $desktop-width) {

  .footer__wrapper {
    width: 80%;
  }

  .footer__questions {
    font-size: 50px;
  }

  .footer__item {
    width: 33%;
    padding: 0;
    margin: 0;
  }

  .footer__item--address {
    width: 33%;
    margin: 0;
    padding: 0 4px;
  }

  .footer__item--phone {
    order: 1;
  }

  .footer__item--mail {
    order: 3;
  }

  .footer__item--address {
    order: 2;
  }

  #map {
    height: 500px;
  }

  .footer__for-tablet {
    margin-top: 35px;
  }

  .footer__contacts {
    padding-bottom: 3%;
  }
}